import React, { useEffect, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import Header from './components/Header';
import ImageCarousel from './components/ImageCarousel';
import Footer from './components/Footer';
import { motion, useAnimation } from 'framer-motion';
import MainImage from './components/MainImage';
import backgroundImg from './assets/TadpoleDiary_background.png';
import DownloadButtons from "./components/DownloadButtons";
import ImageList from "./components/ImageList"; // 이미지 import
import Introduction from './components/Introduction'; // Introduction 컴포넌트 import

function App() {
    // Framer Motion의 애니메이션 제어 훅
    const controls = useAnimation();
    // 현재 섹션을 관리하기 위한 state
    const [section, setSection] = useState(0);
    // 스크롤 이벤트 처리 중인지 여부를 나타내는 state
    const [scrolling, setScrolling] = useState(false);

    // 스크롤 이벤트 처리 함수
    const handleScroll = (direction) => {
        if (!scrolling) {
            setScrolling(true);
            setSection((prevSection) => {
                // 다음 섹션을 계산하고, 섹션이 0과 2 사이에 있도록 제한
                const nextSection = Math.max(0, Math.min(prevSection + direction, 2));
                // Framer Motion을 사용해 애니메이션 시작
                controls.start({
                    y: `-${nextSection * 100}vh`,
                    transition: { duration: 0.5, ease: 'easeInOut' }, // 부드러운 애니메이션을 위해 easeInOut 사용
                });
                setTimeout(() => setScrolling(false), 1000); // 스크롤 다운 이후 1초 동안 스크롤 이벤트를 무시
                return nextSection;
            });
        }
    };

    useEffect(() => {
        // 스크롤 이벤트 리스너 추가
        const onWheel = (e) => {
            e.preventDefault(); // 기본 스크롤 동작 막기
            if (e.deltaY > 0) {
                handleScroll(1); // 스크롤 다운
            } else if (e.deltaY < 0) {
                handleScroll(-1); // 스크롤 업
            }
        };
        window.addEventListener('wheel', onWheel, { passive: false }); // passive: false로 설정하여 preventDefault() 사용

        return () => window.removeEventListener('wheel', onWheel); // 컴포넌트 언마운트 시 이벤트 리스너 제거
    }, [scrolling]); // scrolling state가 변경될 때만 useEffect가 다시 실행되도록 설정


    return (
        <div className="App">
            <Header />
            {/* 애니메이션 컨테이너 */}
            <motion.div
                animate={controls} // 애니메이션 제어를 위한 속성
                initial={{ y: '0vh' }} // 초기 위치 설정
                style={{ height: '100vh', backgroundImage: `url(${backgroundImg})`, backgroundSize: 'cover' }} // 배경 이미지 설정
            >
                {/* 첫 번째 섹션 */}
                <div className="section" style={{ height: '100vh', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', backgroundColor: 'transparent' }}>
                    <MainImage />
                    <DownloadButtons />
                </div>
                {/* 두 번째 섹션 */}
                <div className="section" style={{ height: '100vh', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', backgroundColor: '#6BD2BB' }}>
                    <ImageList />
                </div>
                {/* 세 번째 섹션 */}
                <div className="section" style={{
                    height: '100vh',
                    display: 'flex',
                    flexDirection: 'row', /* 섹션을 가로로 나눔 */
                    backgroundColor: '#ADD8E6'
                }}>
                    <Introduction />
                    <div className="section-right" style={{
                        flex: 1.2, /* 오른쪽 섹션을 일정 비율로 설정 */
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}>
                        <ImageCarousel />
                    </div>
                </div>
            </motion.div>
            <Footer />
        </div>
    );
}

export default App;
